.Sphere {
  line-height: 34px;
  text-align: center;
  display: block;
  border-radius: 50%;
  height: 35px;
  width: 35px;
  margin: 0;
  color: white; }
  .Sphere--blue {
    background: -webkit-radial-gradient(30% 30%, circle, rgba(0, 0, 255, 0.75) 40%, #000037 100%);
    background: -o-radial-gradient(30% 30%, circle, rgba(0, 0, 255, 0.75) 40%, #000037 100%);
    background: radial-gradient(circle at 30% 30%, rgba(0, 0, 255, 0.75) 40%, #000037 100%); }
  .Sphere--black {
    background: -webkit-radial-gradient(30% 30%, circle, rgba(60, 60, 60, 0.75) 40%, black 100%);
    background: -o-radial-gradient(30% 30%, circle, rgba(60, 60, 60, 0.75) 40%, black 100%);
    background: radial-gradient(circle at 30% 30%, rgba(60, 60, 60, 0.75) 40%, black 100%); }
  .Sphere--red {
    background: -webkit-radial-gradient(30% 30%, circle, rgba(255, 0, 0, 0.75) 40%, #370000 100%);
    background: -o-radial-gradient(30% 30%, circle, rgba(255, 0, 0, 0.75) 40%, #370000 100%);
    background: radial-gradient(circle at 30% 30%, rgba(255, 0, 0, 0.75) 40%, #370000 100%); }
  .Sphere--yellow {
    background: -webkit-radial-gradient(30% 30%, circle, #f0f000 40%, #373700 100%);
    background: -o-radial-gradient(30% 30%, circle, #f0f000 40%, #373700 100%);
    background: radial-gradient(circle at 30% 30%, #f0f000 40%, #373700 100%);
    color: black; }
  .Sphere--green {
    background: -webkit-radial-gradient(30% 30%, circle, #00f500 40%, #003700 100%);
    background: -o-radial-gradient(30% 30%, circle, #00f500 40%, #003700 100%);
    background: radial-gradient(circle at 30% 30%, #00f500 40%, #003700 100%);
    color: black; }



