.Ticket {
  font-family: 'Courier New', Courier, monospace, sans-serif;
  position: relative; }
  .Ticket table {
    border-collapse: collapse; }
  .Ticket td,
  .Ticket th {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border: 1px dashed black; }
  .Ticket__footer {
    margin-top: 10px; }
  .Ticket__ticket-number {
    letter-spacing: 2px; }
  .Ticket__close {
    position: absolute;
    right: 24px;
    top: 10px;
    color: darkgrey; }
    .Ticket__close:hover, .Ticket__close:active {
      color: grey;
      text-decoration: none; }



